import React from "react";

import { API_URL } from "./config";

export default class Api extends React.Component {

    constructor() {
        super();
    }

    static async login(data) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            email : data.email,
            password : data.password
        })
            
        var requestOptions = {
            method: "POST",
            headers: headers,
            body: raw,
            redirect: "follow"
        };

        var token = null
        
        return fetch(API_URL + "/api/auth/login", requestOptions)
            .then(response => {
                if (response.headers.get("auth-token")) {
                    return {
                        token: response.headers.get("auth-token")
                    }
                } else {
                    return response.json()
                }
            })
            .then(result => {
                return result
            })
            .catch(error => {
                return error
            });
    }

    static async register(data) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            email: data.email,
            name: data.name,
            password: data.password,
            phone: data.phone,
            address: data.address,
            psc: data.psc,
            city: data.city,
            country: data.country
        });

        var requestOptions = {
            method: "POST",
            headers: headers,
            body: raw,
            redirect: "follow"
        };

        return fetch(API_URL + "/api/auth/register", requestOptions)
            .then(response => response.json())
            .then(result => {
                return result
            })
            .catch(error => {
                return error
            });
    }

    static async getUser(token) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");
        headers.append("auth-token", token);

        var requestOptions = {
            method: "GET",
            headers: headers,
            redirect: "follow"
        };

        return fetch(API_URL + "/api/user/profile", requestOptions)
            .then(response => response.json())
            .then(result => {
                return result
            })
            .catch(error => {
                return error
            });
    }

    static async getProducts(filters) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            filterBy: filters.filterBy,
            sortBy: filters.sortBy,
            limit: filters.limit,
            query: filters.query
        })

        var requestOptions = {
            method: "POST",
            headers: headers,
            body: raw,
            redirect: "follow"
        };

        return fetch(API_URL + "/api/store/products", requestOptions)
            .then(response => response.json())
            .then(result => {
                console.log(result)
                return result
            })
            .catch(error => {
                return error
            });
    }

    static async getProduct(id) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");

        var requestOptions = {
            method: "GET",
            headers: headers,
            redirect: "follow"
        };

        return fetch(API_URL + "/api/store/products/" + id, requestOptions)
            .then(response => response.json())
            .then(result => {
                return result
            })
            .catch(error => {
                return error
            });
    }

    static async createProduct(data, token) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");
        headers.append("auth-token", token);

        var raw = JSON.stringify({
            type: data.type,
            category: data.category,
            price: data.price,
            name: data.name,
            description: data.description,
            eshop: data.eshop
        })

        var requestOptions = {
            method: "POST",
            headers: headers,
            body: raw,
            redirect: "follow"
        };

        return fetch(API_URL + "/api/admin/products/create", requestOptions)
            .then(response => response.json())
            .then(result => {
                return result
            })
            .catch(error => {
                return error
            });
    }

    static async createOrder(ids, id, action, token) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");

        var raw = {}

        if (action === "logged") {
            headers.append("auth-token", token);
            raw = JSON.stringify({
                products: ids
            })
        } else if (action === "temp") {
            raw = JSON.stringify({
                products: ids,
                userId: id
            })
        }

        var requestOptions = {
            method: "POST",
            headers: headers,
            body: raw,
            redirect: "follow"
        };

        return fetch(API_URL + "/api/store/order", requestOptions)
            .then(response => response.json())
            .then(result => {
                return result
            })
            .catch(error => {
                return error
            });
    }

    static async tempUser(data) {
        var headers = new Headers();
        headers.append("Content-Type", "application/json");
        
        var raw = JSON.stringify({
            name: data.name,
            email: data.email,
            phone: data.phone,
            address: data.address,
            city: data.city,
            psc: data.psc,
            country: data.country
        })

        var requestOptions = {
            method: 'POST',
            headers: headers,
            body: raw,
            redirect: 'follow'
        };

        return fetch(API_URL + "/api/store/tempUser", requestOptions)
            .then(response => response.json())
            .then(result => {
                return result
            })
            .catch(error => {
                return error
            });
    }
}