import React from "react";
import { HashRouter, Switch, Route, Link } from "react-router-dom";
import ScrollToTop from "./components/ScrollToTop";

import Home from "./screens/Home";
import Contact from "./screens/Contact";
import Shop from "./screens/Shop";
import Product from "./screens/Product";
import About from "./screens/About";
import Login from "./screens/Login";
import Register from "./screens/Register";
import Profile from "./screens/Profile";
import Cart from "./screens/Cart";
import Checkout from "./screens/Checkout";
import Payment from "./screens/Payment";
import Success from "./screens/Success";
import MobileMenu from "./components/MobileMenu";

export default function Router() {
    return(
        <HashRouter basename="/">
            <ScrollToTop />

            <MobileMenu />

            <Switch>
                <Route exact path="/">
                    <Home />
                </Route>

                <Route exact path="/e-shop">
                    <Shop />
                </Route>

                <Route path="/e-shop/:name-:id" render={(props) => {
                    return ( <Product {...props } /> )
                }} />

                <Route exact path="/kontakt">
                    <Contact />
                </Route>

                <Route exact path="/aromaterapia">
                    <About />
                </Route>

                <Route exact path="/prihlasenie">
                    <Login />
                </Route>

                <Route exact path="/registracia">
                    <Register />
                </Route>

                <Route exact path="/kosik">
                    <Cart />
                </Route>

                <Route exact path="/fakturacne-udaje">
                    <Checkout />
                </Route>

                <Route path="/platba-:orderId" render={(props) => {
                    return ( <Payment {...props } /> )
                }} />

                <Route exact path="/profil">
                    <Profile />
                </Route>

                <Route exact path="/uspech">
                    <Success />
                </Route>
            </Switch>
        </HashRouter>
    )
}