import React from "react";
import { Link, withRouter } from "react-router-dom";

import { isLogged } from "../config/config";

import Header from "../components/Header";
import Footer from "../components/Footer";

import Api from "../config/Api";

import "../styles/login.css";

class Login extends React.Component {

    state = {
        offset: 0,

        email: "",
        password: "",

        message: ""
    }

    constructor() {
        super();
        
        this.login = this.login.bind(this);
    }

    async login() {
        const { email, password } = this.state

        const login = await Api.login({
            email: email.trim(),
            password: password
        })

        if (login.token) {
            // successful login
            const getUser = await Api.getUser(login.token)

            if (getUser.message === "User info retrieved successfully") {
                const user = {
                    ...getUser.user,
                    token: login.token
                }

                localStorage.setItem("user", JSON.stringify(user));
                this.props.history.push("/");
            }
        } else {
            this.setState({ message: login.message })
        }
    }

    componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener('resize', this.updateOffset.bind(this));

        if (isLogged()) {
            this.props.history.push("/profil")
        }
    }
    
    componentDidUpdate() {
        if (isLogged()) {
            this.props.history.push("/profil")
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        return(
            <div className="screen" id="login">
                <Header />

                <div className="content" style={{ paddingTop: this.state.offset }}>
                    <div className="left-panel">
                        <img className="icon" src={require("../assets/family.png")} />
                    </div>

                    <div className="right-panel">
                        <div className="title">Prihlásiť sa do TerraMia</div>
                        <div className="text">
                            Po prihlásení sa do tímu TerraMia budete môcť nakupovať omnoho výhodnejšie, ako keby ste nakupovali anonýmne. Keď ešte nieste členom TerraMia, vytvorte si svoj účet.
                        </div>

                        <input className="field" type="text" value={this.state.email} placeholder="E-mail" onChange={(event) => this.setState({ email: event.target.value})} />
                        <input className="field" type="password" value={this.state.password} placeholder="Heslo" onChange={(event) => this.setState({ password: event.target.value})} />
                        
                        <div className="error-message">{this.state.message}</div>
                        
                        <div className="button-filled" onClick={() => this.login()}>Prihlásiť sa</div>
                        <Link className="button-outline" to="/registracia">Staň sa členom</Link>
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}

export default withRouter(Login);