import React from "react";
import { Link, withRouter } from "react-router-dom";

import { isLogged } from "../config/config";

import Header from "../components/Header";
import Footer from "../components/Footer";

import Api from "../config/Api";

import "../styles/register.css";

class Register extends React.Component {

    state = {
        offset: 0,

        name: "",
        email: "",
        password: "",
        repeatPassword: "",
        phone: "",
        address: "",
        psc: "",
        city: "",
        country: "",

        message: ""
    }

    constructor() {
        super();

        this.register = this.register.bind(this);
    }

    async register() {
        const { name, email, password, repeatPassword, phone, address, psc, city, country } = this.state;

        if (password === repeatPassword) {
            const register = await Api.register({
                name: name.trim(),
                email: email.trim(),
                password: password.trim(),
                phone: phone.trim(),
                address: address.trim(),
                psc: psc.trim(),
                city: city.trim(),
                country: country.trim()
            });

            if (register.message === "The user has been successfully registered") {
                const login = await Api.login({
                    email: email.trim(),
                    password: password.trim()
                })
        
                if (login.token) {
                    // successful login
                    const getUser = await Api.getUser(login.token)
        
                    if (getUser.message === "User info retrieved successfully") {
                        const user = {
                            ...getUser.user,
                            token: login.token
                        }
        
                        localStorage.setItem("user", JSON.stringify(user));
                        this.props.history.push("/");
                    }
                }
            } else {
                this.setState({ message: register.message })
            }
        } else {
            console.log("Passwords do not match...")
        }
    }

    componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener('resize', this.updateOffset.bind(this));

        if (isLogged()) {
            this.props.history.push("/profil")
        }
    }

    componentDidUpdate() {
        if (isLogged()) {
            this.props.history.push("/profil")
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        return(
            <div className="screen" id="register">
                <Header />

                <div className="content" style={{ paddingTop: this.state.offset }}>
                    <div className="left-panel">
                        <img className="icon" src={require("../assets/family.png")} />
                    </div>

                    <div className="right-panel">
                        <div className="title">Staňte sa členom TerraMia</div>
                        <div className="text">
                            Po zaregistrovaní sa do tímu TerraMia budete môcť nakupovať omnoho výhodnejšie, ako keby ste nakupovali anonýmne. Dokonca dostanete prvú skúšobnú vzorku esenciálnych olejov úplne zadarmo!
                        </div>

                        <input className="field" type="text" required value={this.state.name} placeholder="Meno a priezvisko" onChange={(event) => this.setState({ name: event.target.value})} />
                        <br />
                        <input className="field" type="text" required value={this.state.email} placeholder="E-mail" onChange={(event) => this.setState({ email: event.target.value})} />
                        <input className="field" type="password" required value={this.state.password} placeholder="Heslo" onChange={(event) => this.setState({ password: event.target.value})} />
                        <input className="field" type="password" required value={this.state.repeatPassword} placeholder="Zopakovať heslo" onChange={(event) => this.setState({ repeatPassword: event.target.value})} />
                        
                        <br />
                        <input className="field" type="text" required value={this.state.phone} placeholder="Telefónne číslo" onChange={(event) => this.setState({ phone: event.target.value})} />
                        <input className="field" type="text" required value={this.state.address} placeholder="Adresa a číslo domu" onChange={(event) => this.setState({ address: event.target.value})} />
                        <input className="field" type="text" required value={this.state.psc} placeholder="PSČ" onChange={(event) => this.setState({ psc: event.target.value})} />
                        <input className="field" type="text" required value={this.state.city} placeholder="Mesto" onChange={(event) => this.setState({ city: event.target.value})} />
                        <input className="field" type="text" required value={this.state.country} placeholder="Krajina" onChange={(event) => this.setState({ country: event.target.value})} />
                        
                        <div className="error-message">{this.state.message}</div>    
                    
                        <div className="button-filled" onClick={() => this.register()}>Zaregistrovať sa</div>
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}

export default withRouter(Register);